@mixin nav_active {
  position: relative;
  &::after {
    opacity: 0;
    content: '';
    position: absolute;
    bottom: -0.4em;
    left: 0.4em;
    right: 0.4em;
    height: 0.15em;
    background-color: var(--themeColor);
  }

  &.on {
    font-weight: bold;
    &::after {
      opacity: 1;
    }
  }
}

@mixin ellipse($line: 2) {
  display: -webkit-box;
  /*! autoprefixer: ignore next */
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $line;
  overflow: hidden;
}

@mixin flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin select-nav($width: 80px) {
  .nav {
    flex: none;
    border-radius: 15px;
    display: flex;
    border: 1px solid #BBBBBB;
    margin: 10px auto;
    width: min-content;

    &_link {
      position: relative;
      flex: none;
      width: $width;
      height: 30px;
      line-height: 30px;
      text-align: center;
      border-radius: 15px;

      &:hover {
        background-color: #F5F5F5;
      }

      &.on {
        background-color: #BBBBBB;
        color: #fff;
      }

      .lt_ico {
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(20%, -50%);
        border: 1px solid #fff;
        border-radius: 1000px;
        background-color: var(--themeColor);
        line-height: 1;
        padding: 2px 4px;
        color: #fff;
      }
    }
  }
}

.domHome {
  &_artist {
    // padding: 24px;

    &_filter {
      display: flex;
      white-space: nowrap;
      align-items: baseline;
      margin: 0.5em 0;

      .title {
        flex: 0 0 40px;
      }

      .list {
        flex: 1;
        display: flex;
        flex-wrap: wrap;
        .item {
          position: relative;
          margin: 0.2em 0;
          width: 80px;
          &:not(:last-of-type) {
            &::after {
              content: '';
              position: absolute;
              height: 12px;
              top: 50%;
              transform: translate(0, -50%);
              right: 0;
              margin-left: auto;
              background-color: #ddd;
              width: 1px;
            }
          }
        }
      }


      .btn {
        padding: 3px 8px;
      }
    }
  }
}
